import { useMutation } from "react-query";
import { createRedBook } from "../api";

export interface AskRedBookPayload {
    "images": string[];
    "materials": string[];
    "purpose": string;
    "style": string;
    "targetPeople": string;
    "wordCount": number;
    "writer": string;
}

const useAskRedBook = () => {
    return useMutation(async (data: AskRedBookPayload) => {
        return createRedBook<AskRedBookPayload>(data);
    });
}

export default useAskRedBook;