import { useForm } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';
import Tool from "..";
import { FormProvider } from "../../hook-form";
import RHFInput from "../../hook-form/RHFInput";
import RHFArrayInput from "../../hook-form/RHFArrayInput";
import Button from "../../Button";
import { useEffect, useState } from "react";
import useAskRedBook from "../../../hooks/useAskRedBook";
import ReactMarkdown from 'react-markdown'
import CopyToClipboard from 'react-copy-to-clipboard';
import { ArrowPathIcon } from "@heroicons/react/24/outline";

interface FormValuesProps {
    images: string[];
    materials: string[];
    writer: string;
    purpose: string;
    style: string;
    targetPeople: string;
    wordCount: number;
}

const validationSchema = Yup.object().shape({
    images: Yup.array(),
    materials: Yup.array(),
    writer: Yup.string().required(""),
    purpose: Yup.string().required(""),
    style: Yup.string().required(""),
    targetPeople: Yup.string().required(""),
    wordCount: Yup.number().required(""),
});

const Main = () => {
    return (
        <>
            <RHFInput name="writer" label="" />
            <RHFInput name="purpose" label="" />
            <RHFInput name="targetPeople" label="" />
            <RHFInput name="wordCount" label="" />
            <RHFInput name="style" label="" />
            <RHFArrayInput name="materials" label="" />
            <RHFArrayInput name="images" label="" />
        </>
    )
}

const RedBookTool = () => {
    const methods = useForm({
        resolver: yupResolver(validationSchema),
        defaultValues: {
            "images": [
                "iphone",
                "iphone",
                "",
                "1",
                "2",
                "iphone"
            ],
            "materials": [
                "",
                "",
                "128GB256GB512GB1TB",
                " 172 ",
                " XDR 6.1  () OLED 2532 x 1170 460 ppi"
            ],
            "writer": "",
            "purpose": "iphone15",
            "style": "",
            "targetPeople": "",
            "wordCount": 1000
        },
        mode: 'onBlur',
    });

    const {
        handleSubmit,
        setFocus,
        formState: { errors },
    } = methods;

    const mutation = useAskRedBook();
    const [result, setResult] = useState<string>("");
    const [curIndex, setCurIndex] = useState(0);
    const [copyed, setCopyed] = useState(false);

    useEffect(() => {
        if (copyed) {
            setTimeout(() => {
                setCopyed(false);
            }, 1000)
        }
    }, [copyed])

    const onSubmit = async (data: FormValuesProps) => {
        const firstError = Object.keys(errors).reduce((field: any, a: any) => {
            // @ts-ignore
            return !!errors[field] ? field : a;
        }, null);

        if (firstError) {
            setFocus(firstError);
            return;
        }

        setCurIndex(1);
        setResult('...');
        mutation
            .mutateAsync(data)
            .then((e: any) => {
                console.log(e);
                setResult(e.message);
            });
    }

    return (
        <Tool title="" modalWidth="md:w-1/2">
            <div className="modal-mobile-fullscreen sm:max-h-[80vh] sm:h-auto overflow-auto">
                {
                    curIndex === 0 ? (
                        <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                            <Main />
                        </FormProvider>
                    ) : (
                        <ReactMarkdown>{result}</ReactMarkdown>
                    )
                }
                <div className="w-full h-16" />
            </div>
            <div className="absolute bottom-0 left-0 right-0 h-14 flex items-center justify-end pr-4 rounded bg-white shadow-top">
                {
                    curIndex === 0 ? (
                        <>
                            <Button
                                disabled={result === '' || !result}
                                size="small"
                                className="mr-4"
                                text=""
                                onClick={() => setCurIndex(1)}
                            />
                            <Button
                                size="small"
                                color="primary"
                                onClick={handleSubmit(onSubmit)}
                                text=""
                                loading={mutation.isLoading}
                            />
                        </>
                    ) : (
                        <>
                            {/* <ArrowPathIcon className="w-6 mr-4 cursor-pointer" onClick={() => mutation.reset()} /> */}
                            <CopyToClipboard text={result} onCopy={() => setCopyed(true)}>
                                <Button disabled={copyed} size="small" className="mr-4" text={copyed ? '' : ''} />
                            </CopyToClipboard>
                            <Button size="small" text="" onClick={() => setCurIndex(0)} />
                        </>
                    )
                }
            </div>
        </Tool>
    )
}

export default RedBookTool;